/* OPTIONAL: Use a modern or fun font
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap');
*/

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  background: #111;
}

.game-container {
  position: relative;
  min-height: 100vh;
  color: #fff;
  overflow: hidden;
}

/* Casino environment backgrounds */
.casino-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* Slight blur or pattern for a subdued background */
  background: url("https://images.unsplash.com/photo-1600628424867-e6cbecdf57f4")
    no-repeat center center;
  background-size: cover;
  opacity: 0.35;
  filter: blur(3px);
  z-index: 0;
}

/* Neon flicker on top layer */
.neon-lights {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
      circle at 30% 70%,
      rgba(255, 0, 0, 0.2),
      transparent 70%
    ),
    radial-gradient(circle at 70% 30%, rgba(0, 255, 255, 0.2), transparent 70%);
  opacity: 0.5;
  z-index: 1;
  animation: neonFlicker 5s infinite alternate;
}
@keyframes neonFlicker {
  0% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 0.4;
  }
}

/* Subtle back-wall-lights overlay */
.back-wall-lights {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at center,
    rgba(255, 255, 255, 0.2),
    transparent 60%
  );
  opacity: 0.3;
  z-index: 2;
}

/* Header */
.header-bar {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #222;
  padding: 10px 20px;
  border-bottom: 2px solid #c9a100;
}
.game-title {
  font-size: 1.8rem;
  color: #c9a100;
  margin: 0;
  text-transform: uppercase;
}
.balance-area span {
  font-size: 1.2rem;
  font-weight: 600;
}

/* Main content layout */
.main-content {
  position: relative;
  z-index: 10;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

/* Bet Panel */
.bet-panel {
  background: rgba(0, 0, 0, 0.65);
  border: 2px solid #444;
  border-radius: 8px;
  padding: 20px;
  width: 260px;
  margin: 20px;
}
.section-title {
  margin-bottom: 10px;
  color: #c9a100;
  font-weight: bold;
  font-size: 1.2rem;
}
.bet-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}
.bet-color {
  display: flex;
  align-items: center;
}
.bet-color span {
  margin-right: 8px;
  width: 50px;
  font-weight: 600;
  text-transform: capitalize;
}

/* Base color chips for the bet label (Red, White, Blue, Gold) */
.bet-chip {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.6),
    inset 0 1px 3px rgba(255, 255, 255, 0.2),
    inset 0 -2px 2px rgba(0, 0, 0, 0.3);
  border: 2px solid #000;
  overflow: hidden;
  margin-right: 4px;
}
.chip-red {
  background: radial-gradient(circle at 30% 30%, #ff9999, #d40000 70%);
}
.chip-white {
  background: radial-gradient(circle at 30% 30%, #fff, #ccc 70%);
  border: 2px solid #999;
}
.chip-blue {
  background: radial-gradient(circle at 30% 30%, #99caff, #0055aa 70%);
  border: 2px solid #003377;
}
.chip-gold {
  background: radial-gradient(circle at 30% 30%, #ffe066, #c9a100 70%);
  border: 2px solid #7a6100;
}

/* Bet Buttons */
.bet-buttons button {
  margin-right: 4px;
  background: #222;
  color: #c9a100;
  border: 1px solid #c9a100;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  font-weight: 600;
}
.bet-buttons button:hover {
  background: #c9a100;
  color: #111;
}

/* Clear Bets button */
.clear-bets {
  margin-top: 10px;
  width: 100%;
  padding: 6px;
  background: #666;
  color: #fff;
  font-weight: 600;
  border: none;
  border-radius: 4px;
  transition: all 0.2s;
}
.clear-bets:hover {
  background: #ff0000;
}

/* Spin Button */
.spin-button {
  margin: 20px;
  padding: 16px 24px;
  border: 2px solid #c9a100;
  background: #c9a100;
  color: #111;
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.3s;
}
.spin-button:hover:not(:disabled) {
  box-shadow: 0 0 15px #c9a100;
  transform: scale(1.05);
}
.spin-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Wheel Stage */
.wheel-stage {
  position: relative;
  width: 600px;
  height: 500px;
  margin: 20px;
  perspective: 1000px; /* 3D perspective */
}

/* Spotlights above the wheel */
.spotlights {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 2;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.spotlight {
  width: 80px;
  height: 80px;
  background: radial-gradient(rgba(255, 255, 255, 0.5), transparent 70%);
  border-radius: 50%;
  opacity: 0.15;
  animation: moveSpotlight 3s infinite ease-in-out;
}
.spotlight:nth-child(2) {
  animation-delay: 1s;
}
.spotlight:nth-child(3) {
  animation-delay: 2s;
}
@keyframes moveSpotlight {
  0%,
  100% {
    transform: translateY(-15px);
  }
  50% {
    transform: translateY(15px);
  }
}

/* Metallic hardware support behind the wheel */
.wheel-support-frame {
  position: absolute;
  width: 400px;
  height: 200px;
  top: 150px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
}
.support-ring {
  position: absolute;
  top: 30px;
  left: 0;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  background: radial-gradient(#444, #111);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5) inset;
  opacity: 0.6;
  z-index: 1;
}
.support-arm {
  position: absolute;
  width: 60px;
  height: 250px;
  background: linear-gradient(#666, #222);
  border-radius: 30px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.5) inset;
  top: 50px;
  opacity: 0.75;
}
.arm-left {
  left: 15px;
  transform: rotate(-20deg);
}
.arm-right {
  right: 15px;
  transform: rotate(20deg);
}

/* The 3D container for the wheel */
.wheel-3d {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%) rotateX(25deg);
  transform-style: preserve-3d;
  width: 500px;
  height: 500px;
  z-index: 4;
}

/* Optional rotating center "cone" in the wheel */
.center-cone {
  position: absolute;
  top: 150px;
  left: 150px;
  width: 200px;
  height: 200px;
  background: conic-gradient(
    #999 0deg,
    #ccc 30deg,
    #eee 60deg,
    #ccc 90deg,
    #999 120deg,
    #bbb 150deg,
    #eee 180deg,
    #ccc 210deg,
    #999 240deg,
    #ddd 270deg,
    #aaa 300deg,
    #eee 330deg,
    #999 360deg
  );
  border-radius: 50%;
  animation: spinCone 5s linear infinite;
  opacity: 0.8;
  box-shadow: inset 0 0 30px rgba(255, 255, 255, 0.1);
  transform-origin: 50% 50%;
}
@keyframes spinCone {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}

/* The wheel's SVG */
.wheel-svg {
  position: absolute;
  top: 0;
  left: 0;
  background: radial-gradient(#333, #111);
  border-radius: 50%;
  box-shadow: inset 0 0 40px rgba(0, 0, 0, 0.7);
}

/* Multi-color winning wedge flash */
@keyframes wedgeRainbow {
  0% {
    stroke-width: 3px;
    stroke: #fff;
  }
  25% {
    stroke-width: 6px;
    stroke: #0f0;
  }
  50% {
    stroke-width: 3px;
    stroke: #ff0;
  }
  75% {
    stroke-width: 6px;
    stroke: #f00;
  }
  100% {
    stroke-width: 3px;
    stroke: #fff;
  }
}
.winning-wedge {
  animation: wedgeRainbow 1.5s infinite;
}

/* Result Panel */
.result-panel {
  position: relative;
  z-index: 10;
  background: rgba(0, 0, 0, 0.7);
  border: 2px solid #444;
  border-radius: 8px;
  width: 320px;
  margin: 20px auto 30px;
  padding: 15px;
  text-align: center;
  animation: fadeIn 0.5s forwards;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.result-panel h3 {
  margin-bottom: 10px;
  color: #c9a100;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.win-msg {
  color: #0f0;
  font-weight: 600;
}
.lose-msg {
  color: #ff6666;
  font-weight: 600;
}
